@mixin light-theme-mode {}
@mixin dark-theme-mode {
  --bs-white:#132238;
  --bs-bg-dev-linear-gradient:linear-gradient(180deg, rgba(222, 228, 218, 0) 0%, rgba(222, 228, 218, 0.12) 100%);
  --bs-bg-ui-linear-gradient:linear-gradient(90deg, rgba(0, 0, 0, 0.32) 0%, rgba(0, 0, 0, 0.9) 100%);
  --bs-bg-ui-linear-gradient-2:linear-gradient(180deg, #05182E 0%, #0E4789 85.03%);
  --bs-bg-ui-linear-gradient-3:linear-gradient(180deg, #132238 0%, #2B384C 100%);
  --bs-bg-ui-linear-gradient-4:linear-gradient(180deg, rgba(243, 236, 255, 0.04) 0%, rgba(243, 236, 255, 0) 100%), #132238;
  --bs-bg-ui-linear-gradient-5:linear-gradient(180deg, rgba(19, 34, 56, 0.8) 0%, rgba(43, 56, 76, 0.8) 100%), #132238;
  --bs-gray-900:#fff;
  --bs-gray-600:#fff;
  --bs-gray-600-2:#87909D;
  --bs-gray-700:#A5ACB5;
  --bs-bg-dev-services:#05182E;
  --bs-primary-50:#2B384C;
  --bs-gray-900-2:#2B384C;
  --bs-gray-800:#132238;
  --bs-border-one:#2B384C;
  --bs-primary-500-2:#132238;
  --bs-border-two:#000;
  --bs-ellips-1:#2B384C;
  --bs-ellips-2:rgba(255, 217, 26, 0.2);
  --bs-secondary-300:#FFD91A;
  --bs-secondary-100:#2B384C;
  --bs-danger-50:#2B384C;
  --bs-opacity-2:0.2;
}

@mixin dark-mode {
  [data-theme="dark"] &,
  .dark-mode-texts & {
    @content;
  }
}
@mixin light-mode {
  [data-theme="dark"] &,
  .dark-mode-texts & {
    @content;
  }
}

